.my_account {
    display: flex;
}
.table_account {
    width: 100%;
    overflow-x: scroll;
}
.table_account table {
    width: 100%;
    min-width: 1000px;
    border: 1px solid #e1e1e1;
    text-align: center;
}
.table_account table thead th {
    text-align: center;
    background-color: #acacac;
    padding: 0.5rem 0;
    border: 1px solid #e1e1e1;
}
.table_account table tbody tr td {
    border: 1px solid #e1e1e1;
    padding: 0.5rem;
}
