}
.btn_confirm button {
    padding: 0.3rem 1rem;
    margin: 0 0.5rem;
    border: none;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
}
.cancel {
    background-color: #f0f0f0;
    color: #000000;
}
.confirm {
    background-color: #2a9dcc;
    color: #fff;
}
.box_success {
    text-align: center;
}
.box_success .icon_success {
    display: flex;
