@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth !important;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    margin: 0;
}
.modal_table::-webkit-scrollbar-track,
.modal_top_list::-webkit-scrollbar-track,
.list_cart::-webkit-scrollbar-track,
.table_account::-webkit-scrollbar-track {
    background-color: #f5f5f5;
}

.modal_table::-webkit-scrollbar,
.modal_top_list::-webkit-scrollbar,
.list_cart::-webkit-scrollbar,
.table_account::-webkit-scrollbar {
    width: 8px;
    background-color: #f5f5f5;
}

.modal_table::-webkit-scrollbar-thumb,
.modal_top_list::-webkit-scrollbar-thumb,
.list_cart::-webkit-scrollbar-thumb,
.table_account::-webkit-scrollbar-thumb {
    background-color: #000000;
}
#scroll_cart::-webkit-scrollbar {
    width: 5px;
}
html {
    height: 100%;
}
body {
    font-family: 'Roboto', sans-serif;
    scroll-behavior: smooth;
    height: 100%;
    overflow-x: hidden;
}
ul {
    list-style: none;
}
/* REgister */
.ant-modal-body {
    padding-bottom: 1rem !important;
}
