.details_middle {
    display: flex;
}
.details-product {
    flex: 1;
    display: flex;
    flex-wrap: wrap;
}
.details_page {
    margin: 0;
}
.details-title {
    line-height: 2rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ebebeb;
    margin-bottom: 2rem;
}
/* img */
.details-img {
    width: 40%;
    margin-right: 1rem;
}
.img-big {
    position: relative;
    object-fit: contain;
    border: 2px solid #ebebeb;
    z-index: 10;
}
/* small */
.img-small {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1rem;
    overflow-x: hidden;
}
.btn-pre,
.btn-next {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10%;
    cursor: pointer;
}
.img-small-mid {
    flex: 1;
    overflow: hidden;
    margin: 0;
}
.img-small-center {
    display: flex;
    width: 100%;
    justify-content: space-between;
    height: 5rem;
    transition: 0.3s;
}
.img-small-item {
    min-width: 33.33%;
    height: 100%;
    padding: 0 0.5rem;
    cursor: pointer;
}
.img-small-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: 1px solid #ebebeb;
}
/* active img */
.img-active {
    border: 1px solid #000 !important;
}

/* end small img */
/* review */
.reviews-product {
    flex: 1;
}
.reviews-product .reviewsname {
    font-size: 1.7rem;
    font-weight: 500;
    margin-bottom: 0;
}
.reviews-product .star {
    color: #ffc139;
    font-size: 1.1rem;
}
.rev-price {
    color: #000000;
    font-size: 1.7rem;
    font-weight: bold;
}
.more-product {
    opacity: 0.9;
    color: #333;
    z-index: 1;
}
.more-product span {
    font-style: italic;
    font-weight: 500;
    font-size: 1.1rem;
}
.promotion {
    margin-top: 1rem;
    border: 1px dashed #000;
    padding: 0.5rem 0.9rem;
}
.promotion-item {
    margin: 0.5rem 0;
}
.promotion-item i {
    margin-right: 0.5rem;
    color: #64c343;
}
.promotion-item span {
    color: #707070;
}
/* size */
.size {
    margin: 1rem 0;
}
.amount button,
.amount input {
    border: 1px solid #ccc;
    outline: none;
    background-color: #fff;
    line-height: 1.5rem;
}
.amount button {
    padding: 0 1rem;
    cursor: pointer;
}
.amount input {
    margin: 0 0;
    width: 4rem;
    padding-left: 1.5rem;
    padding-right: 1rem;
}
.buy {
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.buy button {
    width: 49%;
    background-color: #080000;
    border: none;
    outline: none;
    color: #fff;
    padding: 0.3rem 2rem;
    border-radius: 5px;
    cursor: pointer;
}
.btn-buynow p,
.btn-call p {
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: bolder;
    margin-bottom: 0;
}
/* end size */
/* description */
.description {
    width: 100%;
    margin-top: 2rem;
}
.tab_title {
    padding: 1rem;
    background-color: #080000;
    color: #fff;
    width: fit-content;
    text-transform: uppercase;
    font-weight: bold;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    margin: 0;
}
.tab_content {
    position: relative;
    padding: 1.5rem;
    border: 1px solid #e5e5e5;
    overflow-y: hidden;
    height: 21rem;
    padding-bottom: 5rem;
}
.tab_content.active {
    height: auto;
}
.tab_table {
    position: relative;
    width: 100%;
    font-size: 1rem;
    border: 1px solid #e5e5e5;
}
.tab_table tr:nth-child(2n + 1) {
    background-color: #f9f9f9;
}

.tab_table td {
    border: 1px solid #ddd;
    padding: 8px;
    color: #000;
}

.tab_content p {
    font-size: 1rem;
}
.tab_content p span {
    font-weight: bold;
}
.tab_content img {
    width: 24rem;
    object-fit: cover;
    height: auto;
}
/* more */
.show_more {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #fff;
    padding-bottom: 1rem;
}
.show_more::before {
    display: block;
    content: '';
    height: 120px;
    position: absolute;
    top: -120px;
    width: 100%;
    left: 0;
    background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0),
        rgba(255, 255, 255, 0.33) 33%,
        rgba(255, 255, 255, 0.8) 83%,
        #fff
    );
}
.show_more.active::before {
    display: none;
}
.show_more span {
    padding: 0.3rem 1rem;
    border: 1px solid #000;
    border-radius: 5px;
    cursor: pointer;
}
.tab_content.active .more-text {
    display: none;
}
.less-text {
    display: none;
}
.tab_content.active .less-text {
    display: block;
}
/* end more */
/* end description */
/* end review */

/* service */
.item-service {
    width: 25%;
    margin-left: 1rem;
    padding: 1.5rem 1rem;
    border: 1px solid #000;
    height: fit-content;
}
.wrap-item {
    display: flex;
    align-items: center;
    color: #978888;
    padding-bottom: 1rem;
    border-bottom: 1px solid #e5e5e5;
    margin-bottom: 1rem;
}
.wrap-item:last-child {
    border: none;
    padding: 0;
}
.wrap-item img {
    object-fit: contain;
    margin-right: 1.5rem;
    width: 2rem;
}
.wrap-item p {
    margin: 0;
    font-weight: bold;
}
/* endservice */
