.scrollToTop {
    position: fixed;
    bottom: 1rem;
    right: 2rem;
}
.scrollToTop button {
    width: 3.5rem;
    height: 3.5rem;
    background-color: #000;
    color: #fff;
    border-radius: 50%;
    border: none;
    outline: none;
    cursor: pointer;
}

/* Navbar */
.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100vh;
    left: -100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10000000;
}
.navbar.active {
    left: 0;
}
.navlist {
    position: absolute;
    top: 0;
    left: -100%;
    width: 20rem;
    height: 100vh;
    background-color: #000;
    box-shadow: 0px 2px 11px 1px rgba(168, 168, 168, 0.54);
    transition: 0.3s;
    z-index: 10000000;
}
.navlist.active {
    left: 0;
    z-index: 100000;
}
.navbar .background {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
}
.navbar .nav_img {
    width: 100%;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid #fff;
}
.navbar .nav_img img {
    margin: 1rem 0;
}
.navbar ul {
    list-style: none;
}
.navbar ul li {
    border-bottom: 1px solid #fff;
}
.navbar ul li .nav_item_flex {
    width: 100%;
    padding: 0.5rem 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #fff;
}
.navbar ul li {
    transition: 1s;
}
.navbar ul li ul {
    display: none;
    transition: 1s;
}
.navbar ul li ul li {
    padding: 0.5rem 2rem;
}
.navbar ul li.nav_active ul {
    display: block;
}
.navbar ul li .nav_link {
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1rem;
    color: #fff;
}

/* End Nav */
.header {
    position: relative;
    width: 100%;
    z-index: 1000;
}
.topbar {
    line-height: 2.4rem;
    background-color: #fff;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.container {
    max-width: 1200px;
    margin: 0 auto;
}
.navitem {
    cursor: pointer;
    .style-span {
        text-transform: uppercase;
        background-image: linear-gradient(-225deg, #231557 0%, #44107a 29%, #ff1361 67%, #fff800 100%);
        background-size: auto auto;
        background-clip: border-box;
        background-size: 200% auto;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        animation: textclip 2s linear infinite;
        display: inline-block;
        font-size: 1rem;
    }
    @keyframes textclip {
        to {
            background-position: 200% center;
        }
    }
}
.account {
    position: relative;
}
.account_icon {
    display: flex;
    justify-content: flex-end;
    position: relative;
}
.acc:hover .btn_login {
    display: block;
}
.btn_login {
    position: absolute;
    display: none;
    left: 0;
    top: 100%;
    padding: 0.5rem;
    width: 13rem;
    background-color: #fff;
    border-radius: 4px;
    z-index: 1000;
}
.btn_login::before {
    position: absolute;
    bottom: 100%;
    content: '';
    left: 2rem;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #fff;
}
.btn_1,
.btn_2 {
    display: block;
    width: 100%;
    border: none;
    outline: none;
    border-radius: 4px;
    cursor: pointer;
}
.btn_1 span,
.btn_2 span {
    color: #fff;
    font-weight: bolder;
    font-size: 1rem;
}
.btn_1 {
    margin-bottom: 0.5rem;
    background-color: #7784d1;
}
.btn_2 {
    background-color: #e1e1e1;
}
.btn_2 span {
    color: #000;
}
.link {
    color: #fff;
    font-size: 1rem;
    text-decoration: none;
    transition: 0.3s;
}
.link span {
    margin-left: 0.1rem;
}
// .link:hover {
//     color: #ffba00;
// }
.style-span:hover {
    transform: scale(1.3);
}
.mid-header {
    position: relative;
    background-color: #3b3d50;
}
.layout {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    padding: 6px 0;
    line-height: 1.7;
}
.layout .icon_nav {
    display: none;
}
.layout .icon_nav i {
    cursor: pointer;
    font-size: 1.5rem;
}
/* search */
.search form {
    position: relative;
    display: flex;
    padding: 3px 0;
    width: 25rem;
}
.search form input {
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    width: 100%;
    height: 2.85rem;
    padding: 0px 1.5rem 0 1.5rem;
    border: none;
    outline: none;
    color: #000;
}
.search form span {
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    position: relative;
    height: 2.85rem;
    padding: 3px;
    background-color: #fff;
}
.btn_search {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    border-radius: 3px;
    background-color: #8eb2e9 !important;
    outline: none;
    border: none;
    cursor: pointer;
}
.bg_cart {
    display: flex;
    position: relative;
}
/* cart hover */
.btn_cart:hover .cart_hover {
    display: block;
}
.cart_hover {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #fff;
    padding: 1rem;
}
.cart_hover .list_cart {
    max-height: 16.5rem;
    overflow-y: scroll;
    padding-right: 0.5rem;
}
.cart_hover .list_cart .list_item {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #000000;
    border-bottom: 1px solid #e1e1e1;
    padding-bottom: 1rem;
    padding-top: 0.5rem;
}
.cart_hover .list_cart .list_item .img img {
    width: 5rem;
    height: 5rem;
}
.cart_hover .list_cart .list_item .info_name {
    font-size: 0.8rem;
    margin-bottom: 0;
}
.cart_hover .list_cart .list_item .info_price {
    font-weight: 700;
    color: #000000;
    margin-bottom: 0.5rem;
}
.quantity span {
    border: 1px solid #e1e1e1;
    outline: none;
    padding: 0.2rem 0.5rem;
    margin: 0;
}
.quantity span:nth-child(2) {
    border-left: none;
    border-right: none;
    padding: 0.2rem 0.9rem;
}
.total_hover {
    color: #000000;
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
    margin-bottom: 1rem;
}
.total_hover span:nth-child(2) {
    font-weight: 700;
}
.btn_cart_hover {
    display: flex;
    flex-direction: column;
}
.btn_cart_hover button {
    color: #fff;
    outline: none;
    border-radius: 3px;
    margin: 0.2rem 0;
    padding: 0.3rem 0;
}
.btn_cart_hover button:first-child {
    background-color: #000000;
    border: 1px solid #000000;
    transition: 0.3s;
}
.btn_cart_hover button:nth-child(2) {
    background-color: #3d4356;
    border: 1px solid #3d4356;
}
.link_check {
    color: #fff;
}
.btn_cart_hover button:first-child:hover {
    background-color: #fff;
}
.btn_cart_hover button:first-child:hover .link_check {
    color: #000000;
}
.btn_cart_hover button:nth-child(2):hover .link_check {
    color: #fff;
}
/*  */
.search_full {
    display: none;
    width: 100%;
}
.search_full form {
    width: 100%;
}
/* end search */
.hotline,
.btn_cart {
    display: flex;
    align-items: center;
    padding-bottom: 0.5rem;
}
.hotline {
    margin-right: 3rem;
}
.hotline .icon,
.btn_cart .icon {
    font-size: 1.5rem;
    border: 1px solid #fff;
    padding: 0.3rem;
    border-radius: 50%;
    margin-right: 0.5rem;
}
.info {
    line-height: 1.2rem;
}
.wrap-main {
    width: 100%;
    background-color: #eceeff;
}
.wrap-main .container ul {
    line-height: 3.5rem;
    display: flex;
    list-style: none;
}
.wrap-main .container ul .first {
    background-color: #475587;
    padding: 0 1rem;
}
.wrap-main .container ul .first i {
    padding-right: 1rem;
}
.wrap-main .container ul li {
    position: relative;
    padding: 0 2rem;
}
.wrap-main .container ul li i {
    padding: 0 0.5rem;
    transition: 0.2s;
}
.wrap-main .container ul li .link span {
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 500;
    transition: 0.2s;
}
.wrap-main .container ul li.active i {
    color: #000b18;
}
.wrap-main .container ul li.active .link span {
    color: #000b18;
}

/* ==========dropdown======== */
.wrap-main .container ul li ul {
    position: absolute;
    left: 0;
    top: 100%;
    width: 15rem;
    display: flex;
    flex-direction: column;
    list-style: none;
    transform-style: preserve-3d;
    transform-origin: top;
    transform: perspective(1000px) rotateX(-90deg) skewX(-7deg);
    background-color: #fff;
    opacity: 0;
    transition: 0.5s;
    box-shadow: 0 1px 2px 2px rgba(0, 0, 0, 0.2);
    z-index: 10;
}
.wrap-main .container ul li ul li {
    position: relative;
    padding: 0 1rem;
    line-height: 3rem;
    cursor: pointer;
    z-index: 1;
}
.wrap-main .container ul li ul li .link span {
    display: block;
    color: #080000;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.wrap-main .container ul li ul li:hover {
    background-color: #080000;
}
.wrap-main .container ul li ul li:hover .link span {
    color: #fff;
}
.wrap-main .container ul li ul li .link span {
    transition: none;
}
.wrap-main .container ul li:hover ul {
    opacity: 1;
    transform: none;
}
